body {
    /* background-color: #111703; */
}

.interface {
    display: flex;
    flex-direction: column;
    align-items: center;

    height: 100%;
    justify-content: space-around;
}

.square {
    position: relative;
}

.square:after {
    content: "";
    display: block;
    padding-bottom: 100%;
}

.square .squareContent {
    position: absolute;
    width: 100%;
    height: 100%;
}

.cellGridWrapper {
    width: 76%;
}

.cellGridWrapper .content {
    position: absolute;
    width: 100%;
    height: 100%;
}

#cellGrid {
    display: grid;
    grid-gap: 1%;
}

.cell {
    /* opacity: .5; */
    transition-property: opacity;
    transition-duration: .3s;
    border-radius: 3px;
    will-change: transform, opacity, background-color;
}

.forceInfected {
    transform: scale(1.3);
}

.cell.infected {
    /* opacity: 1; */
    animation-duration: .5s;
    animation-name: infected-vibe;
    animation-iteration-count: infinite;
    animation-direction: alternate;
    animation-timing-function: cubic-bezier(0.6, -0.28, 0.74, 0.05);
    /* transform: scale(0.9); */
}

@keyframes infected-vibe {
    from {
        transform: scale(0.9);
        /* filter: blur(2px); */
    }

    to {
        transform: scale(1);
        /* filter: blur(1px); */
    }
}

.colorButtons {
    display: flex;
    max-width: 900px;
}

.colorButtons > * {
    margin: 0 1vw;
}
.colorButtons > *:last-child {
    margin-right: 0;
}
.colorButtons > *:first-child {
    margin-left: 0;
}

.colorButton {
    width: 20vw;
}

.colorButton button {
    outline: none;
    border: 0;
    border-radius: 6px;
    box-shadow: 0px 14px 0px 0px;
}

.colorButton button:active {
    transform: translateY(8px);
    box-shadow: 0px 6px 0px 0px;
}

.cell.blue,
.colorButton .blue {
    background-color: #3E2AFF;
}

.cell.green,
.colorButton .green {
    background-color: #1AE8BD;
}

.cell.orange,
.colorButton .orange {
    background-color: #FFAC1C;
}

.cell.red,
.colorButton .red {
    background-color: #E81A43;
}


@media screen and (min-width: 800px) {
    .cellGridWrapper {
        max-width: 60vh;
    }
  }
